*,
*::before,
*::after {
  box-sizing: border-box;
}

body {
  font-family: 'Open Sans', -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu,
    Cantarell, sans-serif;
  line-height: 1.5;
  color: #2a2a2a;
  padding-bottom: 15px;

  position: relative;
}

button {
  cursor: pointer;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

.search-form {
  display: flex;
  justify-content: center;
  padding: 8px;
  margin-bottom: 24px;
}

.search-form .button {
  margin-left: 0;
}
.search-form input {
  margin-left: auto;
}

/* стилі для кнопки LoadMore */
.button {
  display: block;
  margin-left: auto;
  margin-right: auto;
}
.button.is-hidden {
  display: none;
}
.button > .spinner.is-hidden {
  display: none;
}

/* Article List */
.gallery {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  grid-gap: 20px;
  padding: 0;
  margin: 0;
  list-style: none;
  max-width: calc(100vw - 80px);
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 30px;
}

.gallery img {
  display: block;
  width: 100%;
  height: 200px;
  object-fit: cover;
}

.gallery a {
  box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;

  text-decoration: none;
  color: #2a2a2a;
}
/* стилі для інфо під фото */
.info {
  display: flex;
  justify-content: space-around;

  margin-top: 8px;
}

.info-item {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.info-item b {
  display: block;
}
/* теми для кнопки */
.theme-button {
  position: absolute;
  top: 15px;
  right: 15px;

  border-radius: 15px;
  border-color: transparent;
}
.ligth-theme-button {
  background-color: rgb(238, 130, 189);
  color: #2a2a2a;
}
.dark-theme-button {
  background-color: midnightblue;
  color: white;
}
/* теми для сторінки */
.ligth-theme {
  background-color: blanchedalmond;
}
.dark-theme {
  background-color: lightslategray;
}

.photo-card__container {
  transition: background-color 250ms linear;
}

.photo-card__container:hover,
.photo-card__container:focus {
  background-color: #ffffff;
}

/* modal window */
.hide {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;

  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.modal {
  display: flex;
  flex-direction: column;
  justify-content: center;
  gap: 0.4rem;
  width: 450px;
  padding: 1.3rem;
  min-height: 250px;
  position: absolute;
  z-index: 2;
  top: 20%;
  background-color: white;
  border: 1px solid #ddd;
  border-radius: 15px;
}

.modal .flex {
  display: flex;
  align-items: center;
  justify-content: flex-end;
}

.btn-m {
  display: inline-block;
  padding: 0.8rem 1.4rem;
  font-weight: 700;
  background-color: black;
  color: white;
  border-radius: 5px;
  text-align: center;
  font-size: 1em;
}

.btn-open {
  bottom: 150px;
}

.btn-close {
  transform: translate(10px, -20px);
  background: #eee;
  border-radius: 50%;
}

.overlay {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  backdrop-filter: blur(3px);
  z-index: 1;
}
